import { AfterViewInit, Component, OnInit } from '@angular/core';
import { IExperience } from '@shared/models/experience-model';
import { GoogleDriveProvider } from '@shared/services/google-drive-provider.service';
import { SEOService } from '@shared/services/s-e-o.service';
import { BehaviorSubject, Subject } from 'rxjs';
import { ActivatedRoute, ActivationStart, Router } from '@angular/router';

@Component({
  selector: 'app-experience-list',
  templateUrl: './experience-list.component.html',
  styleUrls: ['./experience-list.component.sass'],
})
export class ExperienceListComponent implements OnInit, AfterViewInit {

  public experienceModel: IExperience;
  public isChildActive: boolean = false;
  public previousProject: string
  public nextProject: string
  private currentProjectSystemName$: Subject<string> = new Subject<string>()

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private googleDriveProvider: GoogleDriveProvider,
    private seoService: SEOService,
  ) { }

  ngOnInit(): void {
    this.googleDriveProvider.load().subscribe(data => {
      data.experienceList$.subscribe(data => {
        data.experienceList.sort(((a, b) =>
          a.projectDateRange.to > b.projectDateRange.to ? -1 : 1
        ))
        this.experienceModel = data
        this.currentProjectSystemName$.subscribe(data => {
          const currentProject = this.experienceModel.experienceList.find(item => item.systemName == data)
          this.previousProject = this.previousHandler(currentProject)
          this.nextProject = this.nextHandler(currentProject)
        })
        if (!!this.activatedRoute.firstChild) {
          this.currentProjectSystemName$.next(this.activatedRoute.firstChild.params['_value']['systemName'])
        }
      })
      data.staticPageList$.subscribe(data => {
        this.seoService.updateMeta(
          data.pageList.find(item => item.name == 'experience').seoMeta,
          {isStaticPage: true, isArticle: false}
        )
      })
    })
  }

  ngAfterViewInit(): void {
    this.router.events.subscribe(event => {
      if (event instanceof ActivationStart) {
        this.currentProjectSystemName$.next(event.snapshot.params['systemName'])
      }
    })
  }

  public childInitialized($event: any) {
    this.currentProjectSystemName$.next($event.activatedRoute.params._value['systemName'])
    this.isChildActive = true
  }

  public childDestroyed($event: any) {
    this.isChildActive = false
  }

  private previousHandler (currentProject): string {
    let result: string = null
    if (+currentProject.id > Math.min(...this.experienceModel.experienceList.map(item => {return +item.id})) - 1) {
      const previousProject = this.experienceModel.experienceList.find(item => item.id == +currentProject.id - 1)
      if (previousProject) {
        result = previousProject.systemName
      }
    }
    return result
  }

  private nextHandler (currentProject) {
    let result: string = null
    if (+currentProject.id < Math.max(...this.experienceModel.experienceList.map(item => {return +item.id})) + 1) {
      const nextProject = this.experienceModel.experienceList.find(item => item.id == +currentProject.id + 1)
      if (nextProject) {
        result = nextProject.systemName
      }
    }
    return result
  }
}
