import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.sass']
})
export class SidebarComponent implements OnInit {

  public sidebarStateClass: string = 'sidebar-closed';
  private sidebarOpened: boolean = false;

  constructor() { }

  ngOnInit(): void { }

  public toggleSidebarOnMouseOver() {
    setTimeout(() => this.toggleSidebar(), 1000)
  }

  public toggleSidebar() {
    this.sidebarOpened = !this.sidebarOpened
    if (this.sidebarOpened) {
      this.sidebarStateClass = 'sidebar-opened'
    } else {
      this.sidebarStateClass = 'sidebar-closed'
    }
  }

}
