import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { IExperienceItem } from '@shared/models/experience-model';
import { GoogleDriveProvider } from '@shared/services/google-drive-provider.service';
import { SEOService } from '@shared/services/s-e-o.service';
import { ISEOMetaItem } from '@shared/models/s-e-o-model';


@Component({
  selector: 'app-experience-detail',
  templateUrl: './experience-detail.component.html',
  styleUrls: ['./experience-detail.component.sass']
})
export class ExperienceDetailComponent implements OnInit {

  public experience: IExperienceItem;
  private seoMeta: ISEOMetaItem;

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private dataStorageService: GoogleDriveProvider,
    private seoService: SEOService,
  ) {}

  ngOnInit(): void {
    this.activatedRoute.params.subscribe( params => {
      this.dataStorageService.load().subscribe(data => {
        data.experienceList$.subscribe(data => {
          this.experience = data.experienceList.find(x => x.systemName == params.systemName);
          if (!this.experience) {
            this.router.navigate(['page-not-fount'])
          } else {
            this.seoMeta = {
              id: this.experience.id,
              title: `${this.experience.position} at ${this.experience.company.name} | Markel Kalugin | Senior Python Software Engineer`,
              description: this.experience.customerName
                || this.experience.roleDescription
                || this.experience.duties ? this.experience.duties.slice(0, 140): null,
              keywords: [
                ...this.experience.technologyList.programLanguageList.map(item => item.name),
                ...this.experience.technologyList.frameworkList.map(item => item.name),
                ...this.experience.technologyList.versionControlSystem.map(item => item.name),
                ...this.experience.technologyList.pipelineList.map(item => item.name),
                ...this.experience.technologyList.other.map(item => item.name),
              ],
              author: {firstName: 'Markel', lastName: 'Kalugin', email: 'markel.kalugin@gmail.com'},
              robots: {indexing: 'index', following: 'follow'},
              language: {languageCode: 'en', countryCode:'US'},
              dates: {
                publishedDate: new Date(this.experience.projectDateRange.from),
                modifiedDate: new Date(this.experience.projectDateRange.to),
                expirationDate: null
              }
            }
            this.seoService.updateMeta(
              this.seoMeta,
              {isStaticPage: false, isArticle: true}
            )
          }
        })
      })
    })
  }
}
