import { Pipe, PipeTransform } from '@angular/core';
import * as moment from 'moment';

@Pipe({
  name: 'age'
})
export class AgePipe implements PipeTransform {

  transform(startDate: Date, endDate: Date): string {
    const start = moment(startDate);
    const end = moment(endDate);
    let years = start.diff(end, 'years');
    end.add(years, 'years')
    let months = start.diff(end, 'months');
    end.add(months, 'months')
    let days = start.diff(end, 'days');

    let result: string = '';
    years = Math.abs(years)
    months = Math.abs(months)
    days = Math.abs(days)

    if (years > 0) {
      result = `${years} ${years > 1 ? 'years' : 'year'}`
    }
    if (months > 0) {
      result = `${result} ${months} ${months > 1 ? 'months' : 'month'}`
    }
    if (days > 0) {
      result = `${result} ${days} ${days > 1 ? 'days' : 'day'}`
    }
    return result;
  }

}
